import React from "react";
import { InputNumber, Button, message, List, Space } from "antd";

import "./App.css";
import Keyring from "./utils/kerying";

class App extends React.Component {
  state = {
    num: 1,
    results: [],
  };

  onGenerate = () => {
    const { num } = this.state;
    const results = [];

    if (num <= 0) {
      message.warning("生成个数必须大于0");
      return;
    }

    for (let i = 0; i < num; i++) {
      const seed = Keyring.generateSeed();
      const keyPair = Keyring.generateKeyPair(seed);
      const address = Keyring.address(keyPair.public);
      const addressString = Keyring.addressString(address);
      const data = {
        seed,
        privateKey: keyPair.private,
        publicKey: keyPair.public,
        address,
        addressString,
      };
      results.push(data);
    }

    this.setState({ results });
  };

  onChange = (value) => {
    this.setState({ num: value });
  };

  render() {
    const { num, results } = this.state;

    return (
      <div className="container">
        <h1>批量生成助记词、私钥</h1>

        <div className="optionWrapper">
          <Space>
            <InputNumber
              value={num}
              placeholder="请输入生成个数"
              min={1}
              onChange={this.onChange}
              className="inputNumber"
            />
            <Button
              onClick={this.onGenerate}
              type="primary"
              disabled={num <= 0}
            >
              确定
            </Button>
          </Space>
        </div>

        {results.length > 0 && (
          <List
            bordered
            dataSource={results}
            renderItem={(item) => (
              <List.Item className="listItem">
                <Space direction="vertical">
                  <div className="item">
                    <span className="title">助记词：</span>
                    <span className="value">{item.seed}</span>
                  </div>
                  <div className="item">
                    <span className="title">私钥：</span>
                    <span className="value">{item.privateKey}</span>
                  </div>
                  <div className="item">
                    <span className="title">公钥：</span>
                    <span className="value">{item.publicKey}</span>
                  </div>
                  <div className="item">
                    <span className="title">keyid：</span>
                    <span className="value">{item.address}</span>
                  </div>
                  <div className="item">
                    <span className="title">账户地址：</span>
                    <span className="value">{item.addressString}</span>
                  </div>
                </Space>
              </List.Item>
            )}
          />
        )}
      </div>
    );
  }
}

export default App;
